import * as React from 'react';
import PopupOKCancel from '../common/PopupOKCancel';
import { WriteMode } from '../../modules/write';

export interface AskChangeEditorProps {
  visible: boolean;
  onCancel: () => void;
  onConfirm: () => void;
  convertTo: WriteMode;
}

const AskChangeEditor: React.FC<AskChangeEditorProps> = ({
  visible,
  onCancel,
  onConfirm,
  convertTo,
}) => {
  if (convertTo === WriteMode.MARKDOWN) {
    return (
      <PopupOKCancel
        visible={visible}
        title="  "
        onCancel={onCancel}
        onConfirm={onConfirm}
      >
          ?
        {/* <br />
               
        .
        <br />
        <br />
        <div style={{ fontSize: '0.875rem' }}>
          <b>:</b>       
          .
        </div> */}
      </PopupOKCancel>
    );
  }
  return (
    <PopupOKCancel
      visible={visible}
      title="  "
      onCancel={onCancel}
      onConfirm={onConfirm}
    >
        ?
    </PopupOKCancel>
  );
};

export default AskChangeEditor;
